.cart-icon{
    width:45px;
    height: 45px;
    position:relative;
    display:flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;

    .shopping-icon{
        width:34px;
        height: 34px;        
    }

    .shopping-count{
        position: absolute;
        font-size: 10px;
        font-weight: bold;
        bottom: 12px;
    }
}